import Axios from "axios";

export const GETTING_ITEMS = "GETTING_ITEMS";
// export const LOADING_MAIN = "LOADING_MAIN";
export const DELETE_ITEM = "DELETE_ITEM";
export const ADD_ITEM = "ADD_ITEM";

export const getItem = () => async (dispatch) => {
   let items = await Axios.get(
      "https://assignmentforoffice.herokuapp.com/api/items"
   );

   console.log(items.data);

   dispatch({
      type: GETTING_ITEMS,
      payload: items.data
   });
};

export const deleteItem = (id) => async (dispatch, getState) => {
   console.log(getState().auth.token);
   let delRes = await Axios.delete(
      `https://assignmentforoffice.herokuapp.com/api/items/${id}`,
      {
         headers: {
            "Content-Type": "application/json",
            "x-auth-token": getState().auth.token
         }
      }
   );

   dispatch({
      type: DELETE_ITEM,
      payload: id
   });
};

export const addItem = (name) => async (dispatch, getState) => {
   const body = {
      itemName: name
   };

   let res = await Axios.post(
      "https://assignmentforoffice.herokuapp.com/api/items",
      body,
      {
         headers: {
            "Content-Type": "application/json",
            "x-auth-token": getState().auth.token
         }
      }
   );

   dispatch({
      type: ADD_ITEM,
      payload: res.data
   });
};
