import React, { useState, useEffect } from "react";
import {
   TextField,
   Button,
   IconButton,
   Grid,
   Paper,
   TableBody,
   TableContainer,
   TableRow
} from "@material-ui/core";
import { useDispatch, useSelector } from "react-redux";
import * as Actions from "../../store/actions";
import { Redirect } from "react-router-dom";
import DeleteIcon from "@material-ui/icons/Delete";

export const Main = () => {
   const dispatch = useDispatch();

   const [name, setName] = useState("");

   const isAuth = useSelector(({ auth }) => auth.isAuth);
   const items = useSelector(({ main }) => main.items);
   const isLoading = useSelector(({ main }) => main.isLoading);

   console.log(items);

   useEffect(() => {
      !items && dispatch(Actions.getItem());
   }, []);

   if (!isAuth) {
      return <Redirect to='/' />;
   }

   if (isLoading) {
      return (
         <section
            style={{
               width: "100%",
               height: "100vh",
               display: "flex",
               justifyContent: "center",
               alignItems: "center",
               flexDirection: "column"
            }}
         >
            <h1>Loading..........</h1>
         </section>
      );
   }

   return (
      <div>
         <Button
            onClick={() => dispatch(Actions.logout())}
            variant='contained'
            color='primary'
         >
            Logout
         </Button>
         <section style={{ textAlign: "center", marginTop: 100 }}>
            <h1>Shopping List</h1>
         </section>
         <Grid
            container
            direction='column'
            justify='center'
            alignItems='center'
         >
            <TextField
               value={name}
               onChange={(e) => setName(e.target.value)}
               label='Add Item'
               variant='outlined'
            />
            <Button
               style={{ marginTop: 10 }}
               onClick={() => dispatch(Actions.addItem(name))}
               variant='contained'
               color='primary'
            >
               Add
            </Button>
         </Grid>

         <TableContainer>
            <TableRow>
               <h3>List Items</h3>
            </TableRow>
            <TableBody>
               {items &&
                  items.map((value) => {
                     return (
                        <TableRow>
                           <IconButton
                              style={{
                                 display: "inline",
                                 backgroundColor: "red",
                                 color: "white",
                                 marginTop: 2
                              }}
                              onClick={() =>
                                 dispatch(Actions.deleteItem(value._id))
                              }
                              variant='outlined'
                              size='small'
                           >
                              <DeleteIcon />
                           </IconButton>
                           <p
                              style={{
                                 display: "inline",
                                 marginLeft: 10
                              }}
                           >
                              {value.name}
                           </p>
                        </TableRow>
                     );
                  })}
            </TableBody>
         </TableContainer>
      </div>
   );
};
