import Axios from "axios";

export const REGISTER_SUCCESS = "REGISTER_SUCCESS";
export const REGISTER_ERROR = "REGISTER_ERROR";
export const REGISTER_LOADING = "REGISTER_LOADING";
export const LOGIN_SUCCESS = "LOGIN_SUCCESS";
export const LOGIN_ERROR = "LOGIN_ERROR";
export const LOGOUT = "LOGOUT";
export const LOGIN_LOADING = "LOGIN_LOADING";
export const KEEP_ME_LOGGED = "KEEP_ME_LOGGED";

export const registerProcess = (e) => async (dispatch) => {
   dispatch(registerLoading());
   dispatch({
      type: REGISTER_ERROR,
      payload: null
   });

   try {
      let res = await Axios.post(
         "https://assignmentforoffice.herokuapp.com/api/users",
         e
      );
      console.log(res);

      dispatch({
         type: REGISTER_SUCCESS,
         payload: res.data
      });
      localStorage.setItem("token", res.data.token);
   } catch (error) {
      dispatch({
         type: REGISTER_ERROR,
         payload: error.response.data.msg
      });
   }

   dispatch(registerLoading());
};

export const loginProcess = (e, keepLogin) => async (dispatch) => {
   dispatch(loginLoading());
   dispatch({
      type: LOGIN_ERROR,
      payload: null
   });

   try {
      let res = await Axios.post(
         "https://assignmentforoffice.herokuapp.com/api/auth",
         e
      );
      console.log(res.data);
      dispatch({
         type: LOGIN_SUCCESS,
         payload: res.data
      });
      keepLogin && localStorage.setItem("token", res.data.token);
   } catch (error) {
      dispatch({
         type: LOGIN_ERROR,
         payload: error.response.data.msg
      });
   }

   dispatch(loginLoading());
};

export const registerLoading = () => async (dispatch) => {
   dispatch({
      type: REGISTER_LOADING
   });
};

export const logout = () => async (dispatch) => {
   dispatch({
      type: LOGOUT
   });
};

export const loginLoading = () => async (dispatch) => {
   dispatch({
      type: LOGIN_LOADING
   });
};

export const loggedIn = () => async (dispatch) => {
   dispatch(loginLoading());
   let res = await Axios.get(
      "https://assignmentforoffice.herokuapp.com/api/auth/user",
      {
         headers: {
            "Content-Type": "application/json",
            "x-auth-token": localStorage.getItem("token")
         }
      }
   );
   dispatch({
      type: KEEP_ME_LOGGED,
      payload: res.data
   });
   dispatch(loginLoading());
};
