import React, { useState, useEffect } from "react";
import {
   TextField,
   Button,
   Grid,
   Checkbox,
   FormControlLabel
} from "@material-ui/core";
import { useForm } from "react-hook-form";
import { useDispatch, useSelector } from "react-redux";
import * as Actions from "../../store/actions";
import { Redirect, Link } from "react-router-dom";

export const Login = (props) => {
   const dispatch = useDispatch();
   const { handleSubmit, errors, register } = useForm({
      mode: "onChange"
   });

   const [ErrorMsg, setErrorMsg] = useState(null);
   const [keepLogin, setKeepLogin] = useState(false);
   const isAuth = useSelector(({ auth }) => auth.isAuth);
   const loading = useSelector(({ auth }) => auth.loading);
   const loginError = useSelector(({ auth }) => auth.loginError);

   useEffect(() => {
      loginError && setErrorMsg(loginError);
   }, [loginError]);

   if (isAuth) {
      return <Redirect to='/main' />;
   }

   if (loading) {
      return (
         <section>
            <h1>Loading..........</h1>
         </section>
      );
   }

   console.log(ErrorMsg);

   return (
      <section style={{ textAlign: "center", marginTop: 300 }}>
         <h1>Please Login</h1>
         {ErrorMsg && <h1>{ErrorMsg}</h1>}
         <form
            onSubmit={handleSubmit((value) =>
               dispatch(Actions.loginProcess(value, keepLogin))
            )}
         >
            <Grid
               container
               direction='column'
               justify='center'
               alignItems='center'
            >
               <TextField
                  name='email'
                  inputRef={register({
                     required: "This Field is Requried",
                     pattern: {
                        value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i,
                        message: "Invalid email address"
                     }
                  })}
                  label='Enter Email Address'
                  variant='outlined'
                  color='primary'
               />
               {errors.email && <p> {errors.email.message}</p>}
               <TextField
                  name='password'
                  inputRef={register({
                     required: "This Field is Requried"
                  })}
                  style={{ margin: "20px 0" }}
                  label='Enter Password'
                  variant='outlined'
                  color='primary'
                  type='password'
               />
               {errors.password && <p> {errors.password.message}</p>}
               <FormControlLabel
                  control={
                     <Checkbox
                        checked={keepLogin}
                        value='checkedB'
                        color='primary'
                        onChange={() => setKeepLogin(!keepLogin)}
                     />
                  }
                  label='Keep me Login'
               />
               <Button variant='contained' color='primary' type='submit'>
                  Login
               </Button>
               <Button
                  variant='outlined'
                  color='primary'
                  style={{ margin: 10 }}
               >
                  <Link to='/register'>Register</Link>
               </Button>
            </Grid>
         </form>
      </section>
   );
};
