import React, { useState, useEffect } from "react";
import { TextField, Button, Grid } from "@material-ui/core";
import { useForm } from "react-hook-form";
import { useDispatch, useSelector } from "react-redux";
import * as Actions from "../../store/actions";
import { Redirect, Link } from "react-router-dom";

export const Register = (props) => {
   const dispatch = useDispatch();
   const { handleSubmit, errors, register } = useForm({
      mode: "onChange"
   });

   const [error, setError] = useState(null);

   const isAuth = useSelector(({ auth }) => auth.isAuth);
   const loading = useSelector(({ auth }) => auth.loading);
   const registerError = useSelector(({ auth }) => auth.registerError);

   useEffect(() => {
      registerError && setError(registerError);
   }, [registerError]);

   if (isAuth) {
      return <Redirect to='/main' />;
   }

   if (loading) {
      return (
         <section>
            <h1>Loading..........</h1>
         </section>
      );
   }

   return (
      <section style={{ textAlign: "center", marginTop: 300 }}>
         <h1>Please Register</h1>
         {error && <h1>{error}</h1>}
         <form
            onSubmit={handleSubmit((value) =>
               dispatch(Actions.registerProcess(value))
            )}
         >
            <Grid
               container
               direction='column'
               justify='center'
               alignItems='center'
            >
               <TextField
                  style={{ margin: 10 }}
                  name='name'
                  inputRef={register({
                     required: "This Field is Requried",
                     pattern: {
                        value: /[a-zA-Z][a-zA-Z0-9-_]{3,32}/,
                        message: "Invalid Name"
                     }
                  })}
                  label='Enter Username'
                  variant='outlined'
                  color='primary'
               />
               {errors.name && <p>{errors.name.message}</p>}
               <TextField
                  style={{ margin: 10 }}
                  name='email'
                  inputRef={register({
                     required: "This Field is Requried",
                     pattern: {
                        value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i,
                        message: "Invalid email address"
                     }
                  })}
                  label='Enter Email Address'
                  variant='outlined'
                  color='primary'
               />
               {errors.email && <p>{errors.email.message}</p>}
               <TextField
                  style={{ margin: 10 }}
                  name='password'
                  inputRef={register({
                     required: "This Field is Requried"
                  })}
                  label='Enter Password'
                  variant='outlined'
                  color='primary'
                  type='password'
               />
               <Button
                  style={{ margin: 10 }}
                  variant='contained'
                  color='primary'
                  type='submit'
               >
                  Register
               </Button>
               <Button
                  style={{ margin: 10 }}
                  variant='outlined'
                  color='primary'
               >
                  <Link to='/'>Login</Link>
               </Button>
            </Grid>
         </form>
      </section>
   );
};
